	module.exports = {"data":{"talkJson":{"description":"We're all pretty big fans of Bootstrap. It's a component library that helps us turn our ideas into reality faster than we've ever been able to do in the past. Unfortunately, it's kind of bland (on purpose!), and all the sites on the internet are starting to look the same. At Stripe, we wanted the same power of being able to spin up projects quickly, but without using Bootstrap, specifically. So we made \"Bootstripe\". It's able to be much more opinionated, and allows lots of parallel developers to build consistent user interfaces. I'd love to tell you how to build your own, how to test it, and how to integrate it into your applications.\n\nhttps://jsconfar.com","meta":{"duration":"PT20M4S"},"presenter":{"name":null},"slug":"alex-sexton-your-very-own-component-library-or-jsconfar-2014","source":{"id":"zSYo7m5kGHQ","provider":"youtube"},"tag":[],"title":"Alex Sexton: Your Very Own Component Library | JSConf.ar 2014"},"allTalkJson":null},"pathContext":{"slug":"alex-sexton-your-very-own-component-library-or-jsconfar-2014","tags":[]}}

